import React, { Component } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import './scss/style.scss';

//const loading = (
//  <div className="pt-3 text-center">
//    <div className="sk-spinner sk-spinner-pulse" style={{ marginTop: '30rem' }}></div>
//  </div>
//)

import Spinner from 'react-bootstrap/Spinner'

const loading = () =><div  className="animated fadeIn pt-1 text-center load"><Spinner animation="grow" /></div>;

// Containers Admin & Propriétaire & Visiteur connecté
const TheLayout = React.lazy(() => import('./containers/TheLayout'));
// Containers Visiteurs
const TheLayoutVisiteur = React.lazy(() => import('./containers/TheLayoutVisiteur'));

// Pages-Security
const Login = React.lazy(() => import('./views/pages/login/Login'));
const Register = React.lazy(() => import('./views/pages/register/Register'));
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'));
const Page500 = React.lazy(() => import('./views/pages/page500/Page500'));
const ForgetPwd = React.lazy(() => import('./views/pages/forget-pwd/ForgetPwd'));
const ChangePwd = React.lazy(() => import('./views/pages/forget-pwd/ReinitialiserPwd'));
const ModifierPwd = React.lazy(() => import('./views/pages/forget-pwd/ModifierPwd'));

// Messages
const Echec = React.lazy(() => import('./views/pages/messages/Echec'));
const Success = React.lazy(() => import('./views/pages/messages/Success'));
const PaiementReussi = React.lazy(() => import('./views/pages/messages/PaiementReussi'));
const PaiementEchoue = React.lazy(() => import('./views/pages/messages/PaiementEchoue'));

//Visiteur
const AccueilVisiteurBa = React.lazy(() => import('./views/dashboard/AccueilVisiteurBa'));
const ReservationTerrainVisiteur = React.lazy(() => import('./views/visiteur/ReservationTerrainVisiteur'));

const Apropos = React.lazy(() => import('./views/visiteur/Apropos'));
const NosServices = React.lazy(() => import('./views/visiteur/NosServices'));
const NosTerrains = React.lazy(() => import('./views/visiteur/NosTerrains'));
const NosTournois = React.lazy(() => import('./views/visiteur/NosTournois'));
const NosResultats = React.lazy(() => import('./views/visiteur/NosResultats'));
const NosContacts = React.lazy(() => import('./views/visiteur/NosContacts'));
const Programme = React.lazy(() => import('./views/visiteur/Programme'));

const InscriptionTournoiEntreprise = React.lazy(() => import('./views/visiteur/InscriptionTournoiEntreprise'));
const InscriptionTournoiEcole = React.lazy(() => import('./views/visiteur/InscriptionTournoiEcole'));
const DetailTournoiEntreprise = React.lazy(() => import('./views/visiteur/DetailTournoiEntreprise'));
const DetailTournoiEcole = React.lazy(() => import('./views/visiteur/DetailTournoiEcole'));
const ProgrammeTournoiEntreprise = React.lazy(() => import('./views/visiteur/ProgrammeTournoiEntreprise'));
const ProgrammeTournoiEcole = React.lazy(() => import('./views/visiteur/ProgrammeTournoiEcole'));
const ResultatTournoiEntreprise = React.lazy(() => import('./views/visiteur/ResultatTournoiEntreprise'));
const ResultatTournoiEcole = React.lazy(() => import('./views/visiteur/ResultatTournoiEcole'));

const CGU = React.lazy(() => import('./views/visiteur/CGU'));
const Reglements = React.lazy(() => import('./views/visiteur/Reglements'));

class App extends Component {
  render() {
    return (
      <HashRouter>
          <React.Suspense fallback={loading}>
            <Switch>
              {/* Securite */}
              <Route exact path="/login" name="Connexion" render={props => <Login {...props}/>} />
              <Route exact path="/register" name="Inscription" render={props => <Register {...props}/>} />
              <Route exact path="/404" name="Page 404" render={props => <Page404 {...props}/>} />
              <Route exact path="/500" name="Page 500" render={props => <Page500 {...props}/>} />
              <Route exact path="/forgetpwd" name="Mot de passe oublié" render={props => <ForgetPwd {...props}/>} />
              <Route exact path="/changepwd" name="Changer mot de passe" render={props => <ChangePwd {...props}/>} />
              <Route exact path="/modifierpwd" name="Modifier mot de passe" render={props => <ModifierPwd {...props}/>} />
             
             
              {/* Messages */}
              <Route exact path="/message-echec" name="Message échec" render={props => <Echec {...props}/>} />
              <Route exact path="/message-success" name="Message success" render={props => <Success {...props}/>} />
              <Route exact path="/message-paiement-echoue" name="Message Paiement échoué" render={props => <PaiementEchoue {...props}/>} />
              <Route exact path="/message-paiement-reussi" name="Message Paiement Réussi" render={props => <PaiementReussi {...props}/>} />

              {/* Visiteurs */}
              <Route exact path="/accueil-visiteur"   name="Accueil visiteur" render={props => <AccueilVisiteurBa {...props}/>} />
              
              <Route exact path="/apropos"  name="Nos services" render={props => <Apropos {...props}/>} />
              <Route exact path="/nos-services"  name="Nos services" render={props => <NosServices {...props}/>} />
              <Route exact path="/nos-terrains"  name="Nos terrains" render={props => <NosTerrains {...props}/>} />
              <Route exact path="/nos-tournois"  name="Nos tournois" render={props => <NosTournois {...props}/>} />
              <Route exact path="/nos-resultats"  name="Nos tournois" render={props => <NosResultats {...props}/>} />
              <Route exact path="/nos-contacts"  name="Nos tournois" render={props => <NosContacts {...props}/>} />
              <Route exact path="/programme"  name="Programme" render={props => <Programme {...props}/>} />
              
              <Route exact path="/reservation-terrain-visiteur"  name="Réservation de terrain" render={props => <ReservationTerrainVisiteur {...props}/>} />

              <Route exact path="/inscription-tournoi-inter-entreprise"  name="Inscription Tournoi Inter-Entreprise" render={props => <InscriptionTournoiEntreprise {...props}/>} />
              <Route exact path="/detail-tournoi-inter-entreprise"  name="Détails Tournoi Inter-Entreprise" render={props => <DetailTournoiEntreprise {...props}/>} />
              <Route exact path="/programme-tournoi-inter-entreprise"  name="Programme Tournoi Inter-Entreprise" render={props => <ProgrammeTournoiEntreprise {...props}/>} />
              <Route exact path="/resultat-tournoi-inter-entreprise"  name="Resultat Tournoi Inter-Entreprise" render={props => <ResultatTournoiEntreprise {...props}/>} />

              <Route exact path="/inscription-tournoi-inter-ecole"  name="Inscription Tournoi Inter-Ecole" render={props => <InscriptionTournoiEcole {...props}/>} />
              <Route exact path="/detail-tournoi-inter-ecole"  name="Détails Tournoi Inter-Ecole" render={props => <DetailTournoiEcole {...props}/>} />
              <Route exact path="/programme-tournoi-inter-ecole"  name="Programme Tournoi Inter-Ecole" render={props => <ProgrammeTournoiEcole {...props}/>} />
              <Route exact path="/resultat-tournoi-inter-ecole"  name="Resultat Tournoi Inter-Ecole" render={props => <ResultatTournoiEcole {...props}/>} />

              <Route exact path="/cgu"  name="Conditions générales d'utilisation" render={props => <CGU {...props}/>} />
              <Route exact path="/reglements"  name="Reglements des tournois Oterrain" render={props => <Reglements {...props}/>} />
              
              {/* Layouts */}
              {/* <Route path="/" name="Visiteurs" render={props => <TheLayoutVisiteur {...props}/>} /> */}
              <Route path="/" name="Back office" render={props => <TheLayout {...props}/>} />

            </Switch>
          </React.Suspense>
      </HashRouter>
    );
  }
}
export default App;
